nav {
    position: sticky;
    left: 0;
    top: 0;
    border-bottom: 1px solid rgb(64, 64, 64);
    width: 100%;
}

.nav_wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

/* Logo */
.nav_wrapper .logo {
    display: flex;
    gap: 0.5rem;
    align-items: center;
}

.nav_wrapper .logo_simbol {
    border: 1px solid rgb(64, 64, 64); 
    width: 40px; 
    height: 40px;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.nav_wrapper .logo_simbol .logo_dot {
    width: 32px;
    height: 32px;
    display: block;
    background: indigo;
    border-radius: 100%;
}

.nav_wrapper .logo_title {
    font-family: var(--ff-en);
    font-weight: 500;
    _font-size: 1.5rem;
}

/* Navigation */
.nav_wrapper .navi {
    _display: flex;
    gap: 1.25rem;
    font-family: var(--ff-en);
}

/* Sub Navigation */
.sub_links {
    _display: flex;
    gap: 0.5rem;
    /* align-items: center; */
}

.sub_links a {
    font-family: var(--ff-en);
    border: 1px solid rgb(64, 64, 64);
    padding: 0.25rem 0.75rem;
    border-radius: 0.5rem;
}

.mode_btn{
    font-size:0.625rem;
    _padding: 0 0.5rem;
    border-radius:0.5rem;
    border:1px solid rgb(64, 64, 64);
}
